.footer {
  position: relative;

  height: 360px;

  background-image: url("../images/footer.jpg");
  background-size: cover;
  background-position: center;

  &__wrapper {
    max-width: 1020px;
    margin: 0 auto;
  }
  
  &__logo {
    position: absolute;
    bottom: 40px;
    left: 50%;
    
    display: block;
    
    transform: translateX(-50%);
  }
}
