.person {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 240px;
  transition: 1s;

  &:hover {
    transform: scale(1.1);
    box-shadow: 0 10px 10px $blue-color;
    border-radius: 34px;

    cursor: pointer;
  }
  

  &__avatar {
    padding-top: 10px;
    margin-bottom: 28px;
    max-width: 153px;
    border-radius: 100%;
  }

  &__name {
    font-size: 24px;
    line-height: 30px;
    color: $blue-color;
  }

  &__position {
    font-size: 14px;
    line-height: 27px;
    color: #9fadc2;
  }

  &__text {
    text-align: center;
    font-size: 16px;
    line-height: 27px;
    color: #3e3e3e;
  }

  @media (max-width: 768px) {
    margin-bottom: 48px;
      
    &:last-child {
      margin-bottom: 0;
    }

  }
}
