.contacts {
  @include section;
  display: flex;
  justify-content: space-between;

  &__info {
    display: flex;
    justify-content: space-between;

    &-left,
    &-right {
      display: flex;
      flex-direction: column;
    }

    &-right {
      margin-left: 108px;
      margin-right: 20px;
    }
  }

  &__subtitle {
    margin-bottom: 8px;
    font-size: 14px;
    line-height: 27px;
    color: #9fadc2;
  }

  &__link {
    max-width: 140px;
    margin-bottom: 24px;
    font-size: 16px;
    line-height: 27px;
    color: #3e3e3e;
  }

  &__form {
    width: 413px;
  }

  @media (max-width: 900px) {
    &__info-right {
      margin-left: 48px;
    }
  }

  @media (max-width: 768px) {
    flex-direction: column;

    &__info,
    &__form {
      flex-direction: column;
      width: 100%;
      
      &-right {
        margin-left: 0;
      }
    }
  }
}
